@file:Suppress("EXPERIMENTAL_API_USAGE")

import kotlinx.dom.addClass
import kotlinx.dom.removeClass
import org.w3c.dom.DragEvent
import org.w3c.dom.HTMLDivElement
import org.w3c.dom.HTMLImageElement
import org.w3c.dom.HTMLInputElement
import org.w3c.dom.HTMLSpanElement
import org.w3c.dom.url.URL
import org.w3c.files.File
import org.w3c.files.get
import kotlin.js.Date

class UploadDiv(uploadDiv: HTMLDivElement) {

    private val hiddenInput: HTMLInputElement = uploadDiv.find("input[type='hidden']")!!
    private val paramValue = hiddenInput.value
    private val imageElement: HTMLImageElement? = uploadDiv.find(".yet-upload-image")
    private val progressBar: HTMLDivElement? = uploadDiv.find(".progress-bar")
    private val infoSpan: HTMLSpanElement? = uploadDiv.find(".yet-upload-info")
    private val resultSpan: HTMLSpanElement? = uploadDiv.find(".yet-upload-result")

    private val uploaURL = uploadDiv.attr(UP_URL)!!
    private val uploaURLParam = uploadDiv.attr(UP_PARAM) ?: "file"
    private val uploadViewUrl = uploadDiv.attr(UP_VIEW_URL)!!
    private val uploadViewParam = uploadDiv.attr(UP_VIEW_PARAM)!!
    private val typeURL = uploadDiv.attr(UP_TYPE_IMAGE_URL)
    private val typeURLParam = uploadDiv.attr(UP_TYPE_IMAGE_PARAM)
    private val limitSizeM = uploadDiv.attr(UP_SIZE_LIMIT_M)?.toIntOrNull() ?: 0

    init {
        if (paramValue.isNotEmpty() && paramValue != "0") {
            imageElement?.src = URLBuilder(uploadViewUrl).apply {
                set(uploadViewParam, paramValue)
            }.toString()
        } else {
            logd("Image Error: ", paramValue)
        }
        uploadDiv.ondragover = { e ->
            e.preventDefault()
        }
        uploadDiv.ondrop = { e ->
            e.preventDefault()
            onDropFile(e)
            true
        }
    }

    private fun onDropFile(e: DragEvent) {
        val files = e.dataTransfer?.files ?: return
        if (files.length == 0) return
        val file: File = files[0] ?: return
        logd(file.type, file.size, file.name, file.lastModified)
        val filesize = file.size.toInt()

        val szText: String = readableSize(filesize)

        infoSpan?.textContent = "名称:${file.name}, 大小:${szText}"

        if ("image" in file.type) {
            imageElement?.src = URL.createObjectURL(file)
        } else {
            if (typeURL != null && typeURLParam != null) {
                imageElement?.src = URLBuilder(typeURL).apply { set(typeURLParam, file.type) }.toString()
            } else {
                imageElement?.src = ""
            }
        }
        if (limitSizeM > 0 && filesize > limitSizeM * MB) {
            resultSpan?.failed("文件太大, 不能超过${limitSizeM}M")
            return
        }

        var preTime = Date().getTime()
        HttpPost(uploaURL).formData {
            set(uploaURLParam, file, file.name)
        }.onProgressUpload { pe ->
            val nowDate = Date().getTime()
            if (nowDate - preTime >= 100 || pe.loaded === pe.total) {
                val percent = pe.loaded.toInt() * 100 / pe.total.toInt()
                bsProgressSet(percent)
            }
            preTime = nowDate
        }.onFailed {
            resultSpan?.failed("上传失败: ${it.status} " + it.msg)
        }.requestJsonResult { r ->
            if (r.OK) {
                resultSpan?.success("上传成功")
                hiddenInput.value = (r.dataObject["id"]).toString()
            } else {
                resultSpan?.failed("上传失败" + r.msg)
            }
        }

    }

    private fun bsProgressSet(v: Int) {
        progressBar?.apply {
            style.width = "$v%"
            attr("aria-valuenow", v.toString())
            textContent = "$v%"
        }
    }

    companion object {
        private const val UP_SIZE_LIMIT_M = "data-upload-limit-m"
        private const val UP_URL = "data-upload-url"
        private const val UP_PARAM = "data-upload-param"
        private const val UP_VIEW_URL = "data-upload-view-url"
        private const val UP_VIEW_PARAM = "data-upload-view-param"
        private const val UP_TYPE_IMAGE_URL = "data-upload-type-image-url"
        private const val UP_TYPE_IMAGE_PARAM = "data-upload-type-image-param"
    }
}

private fun HTMLSpanElement.success(text: String) {
    this.textContent = text
    this.removeClass("text-danger")
    this.addClass("text-success")
}

private fun HTMLSpanElement.failed(text: String) {
    this.textContent = text
    this.removeClass("text-success")
    this.addClass("text-danger")
}

private fun readableSize(filesize: Int): String {
    return when {
        filesize > GB -> (filesize * 1.0 / GB).toString().keep(2) + "G"
        filesize > MB -> (filesize * 1.0 / MB).toString().keep(2) + "M"
        filesize > KB -> (filesize * 1.0 / KB).toString().keep(2) + "K"
        else -> filesize.toString() + "字节"
    }
}